import React from 'react';
import {
  Switch,
  Route,
} from "react-router-dom";
import Main from './pages/main/main';
import Favorites from './pages/favorites/favorites';
import NavBar from './components/navBar/navBar';


function App() {

  return (
    <div >
      <NavBar />
      <Switch>
        <Route exact path="/">
          <Main />
        </Route>
        <Route path="/favorites">
          <Favorites />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
